import React from 'react'
import { useNavigate } from 'react-router-dom';
import Logo from "../Images/Logo1.jpg"

const Navhome = () => {
  let Navigate = useNavigate();

  const reset = ()=>{
    
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("userId");
    sessionStorage.removeItem("stime");
    sessionStorage.removeItem("etime");
    sessionStorage.removeItem("name");
    sessionStorage.removeItem("elapsed");
    Navigate("/")
  }
  return (
    <div>
  <nav className="navbar navbar-expand-lg navbar-light bg-secondary bg-opacity-10 m-0 p-0">
  <div className="container-fluid ps-0">
    <a className="navbar-brand p-0" href="/adminhome"><img className="Logo me-4" src={Logo} alt="" />{sessionStorage.getItem("name")}</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav ms-auto">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="/dashboard">Home</a>
        </li>
        
        <li className="nav-item">
          <button onClick={reset} className='bg-secondary bg-opacity-10 text-dark border-0 mt-2'>Signout</button>
        </li>
       
       
      </ul>
    </div>
  </div>
</nav>
    </div>
  )
}


export default Navhome