import React from 'react'
import { useNavigate } from 'react-router-dom';
import Logo from "../Images/Logo1.jpg"

const Navadmin = () => {
    let Navigate = useNavigate();

    const reset = ()=>{
      Navigate("/")
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("userId");

    }


  return (
    <div>
  <nav className="navbar navbar-fixed-top navbar-expand-lg navbar-light bg-secondary bg-opacity-10 p-0 m-0">
  <div className="container-fluid ps-0">
    <div className="navbar-header">
        <a className="navbar-brand" href="/adminhome"><img src={Logo} alt="" className="Logo me-4"/>{sessionStorage.getItem("name")}</a>
    </div>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav ms-auto">
        <li className="nav-item">
          <a className="nav-link active " aria-current="page" href="/adminhome">Home</a>
        </li>
        <li className="nav-item">
          <a className="nav-link active " aria-current="page" href="/viewusers">View-Users</a>
        </li>
        <li className="nav-item">
          <a className="nav-link active " href="/Adduser">Add-User</a>
        </li>

        <li className="nav-item">
          <button onClick={reset} className='btn nav-link active border-secondary border-opacity-10'>Signout</button>
        </li>
       
       
      </ul>
    </div>
  </div>
</nav>
    </div>
  )
}

export default Navadmin