import axios from 'axios'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Navadmin from './Navadmin'


const Adduser = () => {

 
    let Navigate = useNavigate();
    
    const[data,setData] = useState({
       
        token:sessionStorage.getItem("token"),
        name:"",
        email:"",
        password:""
        
      })

      const inputHandler = (event)=>{
        const {name,value}=event.target 
        setData(
          (previousState)=>({
            ...previousState,
            [name]:value
          })
        )
      }
    
      const users =()=>{
        // console.log(data)
        axios.post("/api/adduser",data)
        .then((response)=>{
          console.log(response.data)
          if (response.data.Status=="User added successfully") {
            console.log("Successfully added the User")
            alert("Successfully added the User")
            setData(
              { 
                token:sessionStorage.getItem("token"),
                name:"",
                email:"",
                password:""
              })
              Navigate("/viewusers")
            } 
          else {
            alert("Error")
          }
        })
      .catch((err)=>{
        console.log(err)
      })
      }
    
    
  return (
   <div>
        <Navadmin/>
        <div className="container">
            <form action="">
            <div className="row justify-content-center">
                <div className="border border-dark shadow-lg bg-secondary bg-opacity-10 rounded p-5 g-4 col col-8 col-sm-8 col-md-6 col-lg-5 col-xl-4 col-xxl-4">
                    <div className="row g-4">

                        <h3 className="text-center text-secondary mt-3 ">Add a user</h3>

                        <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                            <label htmlFor="" className="form-label">Name</label>
                            <input type="text" className="form-control" 
                            placeholder='Enter user name'
                            onChange={inputHandler}
                            value={data.name}
                            name="name"
                            />
                        </div>

                        <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                            <label htmlFor="" className="form-label">Email id</label>
                            <input type="email" className="form-control" 
                            placeholder='Enter email id'
                            onChange={inputHandler}
                            value={data.email}
                            name="email"
                            />
                        </div>

                        <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                            <label htmlFor="" className="form-label">Password</label>
                            <input type="text" className="form-control" 
                            placeholder='Enter the password'
                            onChange={inputHandler}
                            value={data.password}
                            name="password"/>
                        </div>
                                    
                        <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                            <button onClick={users} className="btn bg-dark bg-opacity-75 text-light" style={{ width:'100%'}}>Submit</button>
                        </div>

                    </div>

                </div>
            </div>
           </form>
        </div>
        
   </div>
  )
}


export default Adduser