import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import { DateRangePicker } from 'react-date-range';
import moment from 'moment';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';



const Trackeroutput = () => {
    
    const [myentries,setMyentries] = useState([])
    const [selectedentries,setSelectedentries] = useState([])

    const [startDate, SetStartDate] = useState(new Date());
    const [endDate, SetEndDate] = useState(new Date())

    useEffect(
      ()=>{
        tracker()
      },[]
    )
  
    const tracker= ()=>{

    var data = {"userId":sessionStorage.getItem("userId")}
    console.log(data);
     
    axios.post("/api/viewmyentries",data)
      .then(
        (res)=>{
          setMyentries(res.data)
          setSelectedentries(res.data)
        }
      )
      .catch()
    
      }

    const deleteEntry = async(id)=>{
      console.warn(id);
      let result = await fetch(`/api/deleteentry/${id}`, {method:"delete"}
  )
     result = await result.json();
     if (result){
      console.log("success")
      console.log(result)
      tracker()
     }
  };

  const handleSelect = (date)=>{

      let filtered = myentries.filter((entry)=>{
      let entryDate = moment(entry.Date).format("MM/DD/YYYY")

      return(entryDate>=moment(date.selection.startDate).format("MM/DD/YYYY") && entryDate<=(moment(date.selection.endDate).format("MM/DD/YYYY")))
     });
        SetStartDate(date.selection.startDate);
        SetEndDate(date.selection.endDate);
            setSelectedentries(filtered)
     };


  const selectionRange = {
          startDate: startDate,
          endDate: endDate,
          key: 'selection'
    }

    return (
        <div>
            <div className="container">
                <div className="row">
                <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                <h5 className='mt-4 text-dark text-center border rounded p-2 bg-secondary bg-opacity-10'>{sessionStorage.getItem("name")}'s Dashboard</h5>
                <div className="accordion accordion-flush" id="accordionFlushExample">
                <div className="accordion-item ">
                <h2 className="accordion-header" id="flush-headingOne">
                  <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseOne" aria-expanded="false" aria-controls="flush-collapseOne">
                Filter Data
                  </button>
                  </h2>
                  <div id="flush-collapseOne" className="accordion-collapse collapse" aria-labelledby="flush-headingOne" data-bs-parent="#accordionFlushExample">
                  <div className="accordion-body">

                  <DateRangePicker
                  ranges={[selectionRange]}
                  onChange={handleSelect}/>
                    </div>
                    <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                  <button onClick={()=>{setSelectedentries(myentries)}} className='btn bg-secondary bg-opacity-50 rounded align-bottom'>Clear filter</button>
                  </div>
                  </div>
                </div>
              </div>

            <div className="container">
                        
          <table className="table table-striped table-bordered table-hover mt-2">
          <thead className='text-center bg-secondary bg-opacity-25 border-dark'>
            <tr>
              <th scope="col" className='col col-12 col-sm-2 col-md-2 col-lg-2 col-xl-2 col-xxl-2'>Date-mm/dd/yyyy</th>
              <th scope="col" aria-sort='true' className='text-center col col-12 col-sm-2 col-md-2 col-lg-2 col-xl-2 col-xxl-2'>Project</th>
              <th scope="col" className='text-center col col-12 col-sm-2 col-md-2 col-lg-2 col-xl-2 col-xxl-2'>Task</th>
              <th scope="col" className='text-center col col-12 col-sm-2 col-md-2 col-lg-2 col-xl-2 col-xxl-2'>Start:End</th>
              <th scope="col" className='text-center col col-12 col-sm-2 col-md-2 col-lg-2 col-xl-2 col-xxl-2'>Project total</th>
              <th scope="col" className='text-center col col-12 col-sm-2 col-md-2 col-lg-2 col-xl-2 col-xxl-2'>Operations</th>
            
            </tr>
          </thead>
          <tbody>
            {selectedentries.map(
                (value,index)=>{
                
              return <tr key={value._id}>
              
              <td className='text-center'>{value.Date}</td>
              <td className='text-center'>{value.Project}</td>
              <td className='text-center'>{value.Task}</td>
              <td className='text-center'>{value.Start+" - "+value.End}</td>
              <td className='text-center'><h6>{value.Timer}</h6></td>
              <td className='text-center'>
              <Link to = {"/updateentry/"+(value._id)} className='btn'><EditIcon/></Link>
              <button onClick={()=>deleteEntry(value._id)} className='btn'><DeleteIcon/></button>
       
              </td>
             </tr>
        
                })
              }
        
          </tbody>
        </table>
        </div>
                    </div>
                </div>
            </div>
            </div>
            
          )
        }


export default Trackeroutput