import axios from 'axios';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import Navbar from './Navbar';

const Signin = () => {
  
    let Navigate= useNavigate();

  const[data,setData] = useState(
    {
      email:"",
      password:""
    })

   const inputHandler = (event)=>{
      const {name,value}=event.target 
      setData(
        (previousState)=>({
          ...previousState,
          [name]:value
        })
      )
    }

    const loginval = async ()=>{
      
      // console.log(data)
      var result = await axios.post(`/api/signin`,data)
      .then(
        (res)=>{
          if (res.data.status=="success") {
            // console.log(res)
            let token = res.data.token
            // let userId = res.data.data._id
            // let name = res.data.data.name
           
            sessionStorage.setItem("token",token)
            // sessionStorage.setItem("userId",userId)
            // sessionStorage.setItem("name",name)

            if(res.data.data.email=="admin@gmail.com"){
                alert("admin login")
                Navigate("/adminhome")
            }
            else{
              alert("valid user")
              Navigate("/dashboard")
            }

          } else {
            alert("invalid credentials")
          }
      
         }
      )
      .catch((err)=>{
        console.log(err)
      })
  
    }

  return (
    <div>
      <Navbar/>
    {/* <form action=""> */}
    
      
    <div className="container">
    <div className="row justify-content-center">
    <h3 className= "text-center text-secondary mt-4 ">Dashboard Login</h3>
    
        <div className="border border-dark shadow-lg bg-secondary bg-opacity-10 rounded p-5 g-4  col col-6 col-sm-6 col-md-6 col-lg-5 col-xl-4 col-xxl-4">
                <div className="row g-3">
                
                    <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                        <label htmlFor="" className="form-label">Email id</label>
                        <input type="email" className="form-control"
                        required ={true}
                        name='email'
                        placeholder='Enter your email address'
                        value={data.email}
                        onChange={inputHandler} />
                    </div>

                    <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                        <label htmlFor="" className="form-label">Password</label>
                        <input type="password"  id="" className="form-control" 
                        required ={true}
                        placeholder='Enter your password'
                        value={data.password} 
                        name='password'
                        onChange={inputHandler}
                         />
                    </div>

                    <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12 mt-4">
                        <button onClick={loginval} className="btn bg-dark bg-opacity-75 text-light" style={{ width:'100%'}}>Login</button>
                    </div>

                    
        
                </div>
                
        </div>
        
    </div>
   </div>
   {/* </form> */}
   </div>
  )
}

export default Signin