import axios from 'axios'
import React, { useEffect, useState } from 'react'
import imgemp from "../Images/employee1.jpg"
import Navadmin from './Navadmin'

const Viewuser = () => {
const [employee, setEmployee]= useState([])

  useEffect(()=>{
    viewemployees()
  },[])

  const viewemployees=()=>{
    axios.get("/api/viewusers")
    .then((response)=>{
      console.log(response.data)
      setEmployee(response.data)
    })
    .catch()
  };


  const deleteUser = async(id)=>{
    console.warn(id);
    let result = await fetch(`/api/deleteuser/${id}`, {method:"delete"}
)
   result = await result.json();
   if (result){
    console.log("success")
    console.log(result)
    viewemployees()
   }
   else{console.log("failed")}
};
  return (
    <div>
    <Navadmin/>
    <div className="container" >
      <div className="row">
        <div className="col col-12-col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12 g-4 ">
            <div className="row g-4">
                {employee.filter(entry=>(entry._id!="63e4d34a0efbb16d3ad3dc7e")).map(
                    (value,index)=>{
                        return    <div className="col col-12-col-sm-6 col-md-4 col-lg-3 col-xl-3 col-xxl-3">
                        <div class="card">
                  {/* <img src={imgemp} class="card-img-top cardimg" alt="..."/> */}
                  <div class="card-body">
                    <h5 class="card-title text-center">{value.name}</h5>
                    <p class="card-text text-center">{value.email}</p>
                    <button onClick={()=>deleteUser(value._id)} className='bg-secondary border-light rounded mt-1 text-light text-center' style={{ width:'100%'}}>Delete-user</button>
                  </div>
                </div>
                    </div>
                    }
                )
                }
             

            </div>
     
        </div>
        
      </div>
    </div>
  </div>
  )
}

export default Viewuser